import React from 'react';
import PropTypes from 'prop-types';
import { iconList } from '../../../../utilities/icons'
//import icomoonSelection from './../../../../font/icons/selection.json'
import './skl-ds-icon.css';
import './../../../../font/icons/style.css';

//const iconList = icomoonSelection.icons.map((icon) => icon.properties.name);


console.log("iconLisssst", iconList)



export const Icon = ({ iconName, color, size}) => {
  return (
    <i 
      className={`icon-${iconName} skl-ds-icon-${size}`}
      style={{ color }} 
      aria-hidden="true"
    ></i>
  );
};

Icon.propTypes = {
  iconName: PropTypes.oneOf(iconList).isRequired,
  color: PropTypes.string, 
  size: PropTypes.oneOf(['small','medium', 'large', 'xlarge'])
};

Icon.defaultProps = {
  iconName: 'alarm',
  color: '#000', 
  size: 'large'
};
